




























































import Vue from 'vue';

export default Vue.extend({
    name: 'RomaHeader',
    props: {
        info: {
            type: Object
        },
        social: {
            type: Array,
            default () {
                return [
                    {icon: 'instagram', url: 'http://instagram.com/'},
                    {icon: 'facebook', url: 'http://facebook.com/'},
                    {icon: 'youtube', url: 'http://youtube.com/'},
                    {icon: 'twitter', url: 'http://twitter.com/'},
                ]
            }
        },
        languages: {
            type: Array,
            default () {
                return [
                    {title: 'Español', short: 'es', icon: 'https://image.flaticon.com/icons/png/512/197/197593.png'}
                ]
            }
        }
    },
    data () {
        return {
            selectedLanguage: this.languages[0],
            contacts: [
                {icon: 'phone-volume', text: `Teléfono SAC: ${this.info.sac}`},
                {icon: 'mobile', text: `Teléfono Mobil: ${this.info.phone}`},
                {icon: 'at', text: `Correo Electrónico: ${this.info.email}`, url: `mailto://${this.info.email}`},
                {icon: 'map-marked-alt', text: `Ver Ubicación`, url: `https://www.google.co.ve/maps/@${this.info.location.lat},${this.info.location.lon}`}
            ],
        }
    },
});
